.experience-card {
  background:
    linear-gradient(135deg, rgba(10, 32, 52, 0.65), rgba(8, 16, 28, 0.65)),
    radial-gradient(circle at top, rgba(0, 255, 170, 0.12), transparent 55%),
    rgba(13, 17, 23, 0.35);
  backdrop-filter: blur(36px) saturate(220%) brightness(1.15);
  -webkit-backdrop-filter: blur(36px) saturate(220%) brightness(1.15);
  border: 1px solid rgba(99, 164, 255, 0.45);
  border-radius: 26px;
  margin-bottom: 24px;
  overflow: hidden;
  transition: all 0.6s cubic-bezier(0.23, 1, 0.32, 1);
  cursor: pointer;
  position: relative;
  transform-style: preserve-3d;
  will-change: transform, box-shadow;
  backface-visibility: hidden;
  -webkit-backface-visibility: hidden;
  box-shadow:
    0 25px 70px rgba(7, 12, 22, 0.55),
    0 10px 40px rgba(66, 133, 244, 0.16),
    0 0 0 1px rgba(66, 133, 244, 0.28),
    inset 0 1px 0 rgba(240, 246, 252, 0.12),
    inset 0 -1px 0 rgba(0, 0, 0, 0.24);
}

.experience-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg,
    transparent,
    rgba(240, 246, 252, 0.08),
    transparent);
  transition: left 1s ease;
  pointer-events: none;
  z-index: 1;
}

.experience-card::after {
  content: '';
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 20% 20%, rgba(66, 133, 244, 0.08) 0%, transparent 55%),
    radial-gradient(circle at 80% 80%, rgba(0, 255, 136, 0.06) 0%, transparent 55%);
  mix-blend-mode: screen;
  pointer-events: none;
  z-index: 0;
}

.experience-card > * {
  position: relative;
  z-index: 2;
}

.experience-card:hover {
  transform: translateY(-12px) scale(1.015) rotateX(2deg);
  border-color: rgba(132, 197, 255, 0.7);
  background:
    linear-gradient(135deg, rgba(12, 38, 63, 0.78), rgba(9, 20, 35, 0.78)),
    radial-gradient(circle at top, rgba(0, 255, 170, 0.18), transparent 60%),
    rgba(13, 17, 23, 0.45);
  box-shadow:
    0 35px 90px rgba(7, 12, 22, 0.65),
    0 16px 48px rgba(66, 133, 244, 0.22),
    0 0 0 1px rgba(132, 197, 255, 0.55),
    0 0 50px rgba(0, 255, 170, 0.2),
    inset 0 1px 0 rgba(240, 246, 252, 0.16),
    inset 0 -1px 0 rgba(0, 0, 0, 0.26);
}

.experience-card:hover::before {
  left: 100%;
}

.experience-card.expanded {
  transform: translateY(-8px) scale(1.01);
  border-color: rgba(132, 197, 255, 0.62);
  background:
    linear-gradient(135deg, rgba(11, 34, 56, 0.82), rgba(9, 20, 35, 0.8)),
    radial-gradient(circle at top, rgba(0, 255, 170, 0.2), transparent 65%),
    rgba(13, 17, 23, 0.45);
  backdrop-filter: blur(36px) saturate(220%) brightness(1.12);
  -webkit-backdrop-filter: blur(36px) saturate(220%) brightness(1.12);
  box-shadow:
    0 30px 80px rgba(7, 12, 22, 0.6),
    0 14px 42px rgba(66, 133, 244, 0.2),
    0 0 0 1px rgba(132, 197, 255, 0.52),
    0 0 40px rgba(0, 255, 170, 0.18),
    inset 0 1px 0 rgba(240, 246, 252, 0.14),
    inset 0 -1px 0 rgba(0, 0, 0, 0.24);
}

.card-header {
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: relative;
}

.card-main-info {
  flex: 1;
}

.job-title-section {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
  flex-wrap: wrap;
}

.job-title {
  font-size: 1.4rem;
  font-weight: 800;
  margin: 0;
  line-height: 1.25;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

.status-badge {
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 0.75rem;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  backdrop-filter: blur(10px) saturate(180%);
  -webkit-backdrop-filter: blur(10px) saturate(180%);
  border: 1px solid transparent;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.1);
  transition: all 0.3s ease;
}

.status-badge.current {
  background: rgba(0, 255, 136, 0.15);
  color: #00ff88;
  border-color: rgba(0, 255, 136, 0.5);
  position: relative;
  box-shadow:
    0 4px 16px rgba(0, 255, 136, 0.2),
    0 2px 8px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(240, 246, 252, 0.1),
    inset 0 -1px 0 rgba(0, 0, 0, 0.1);
}

.status-badge.current::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg,
    rgba(0, 255, 136, 0.08) 0%,
    rgba(0, 255, 136, 0.02) 100%);
  border-radius: 20px;
  pointer-events: none;
  z-index: 0;
}

.status-badge.current span {
  position: relative;
  z-index: 1;
}

.status-badge.since-2021,
.status-badge.since-2020 {
  background: rgba(255, 193, 7, 0.15);
  color: #ffc107;
  border-color: rgba(255, 193, 7, 0.5);
  position: relative;
  box-shadow:
    0 4px 16px rgba(255, 193, 7, 0.2),
    0 2px 8px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(240, 246, 252, 0.1),
    inset 0 -1px 0 rgba(0, 0, 0, 0.1);
}

.status-badge.since-2021::before,
.status-badge.since-2020::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg,
    rgba(255, 193, 7, 0.08) 0%,
    rgba(255, 193, 7, 0.02) 100%);
  border-radius: 20px;
  pointer-events: none;
  z-index: 0;
}

.status-badge.since-2021 span,
.status-badge.since-2020 span {
  position: relative;
  z-index: 1;
}

.company-info {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.company-name {
  font-size: 1.1rem;
  font-weight: 600;
  color: #e0e0e0;
  margin: 0;
  line-height: 1.4;
}

.duration-info {
  display: flex;
  gap: 16px;
  align-items: center;
  flex-wrap: wrap;
}

.duration {
  font-size: 0.9rem;
  color: #8ab4f8;
  font-weight: 600;
  padding: 10px 18px;
  background: rgba(66, 133, 244, 0.12);
  backdrop-filter: blur(20px) saturate(180%) brightness(1.2);
  -webkit-backdrop-filter: blur(20px) saturate(180%) brightness(1.2);
  border-radius: 16px;
  border: 1px solid rgba(66, 133, 244, 0.4);
  position: relative;
  box-shadow:
    0 4px 16px rgba(66, 133, 244, 0.15),
    0 2px 8px rgba(0, 0, 0, 0.1),
    inset 0 1px 0 rgba(240, 246, 252, 0.1),
    inset 0 -1px 0 rgba(0, 0, 0, 0.1);
}

.duration::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(135deg,
    rgba(66, 133, 244, 0.05) 0%,
    rgba(26, 115, 232, 0.02) 100%);
  border-radius: 16px;
  pointer-events: none;
  z-index: 0;
}

.duration span {
  position: relative;
  z-index: 1;
}

.period {
  font-size: 0.85rem;
  color: #b0b0b0;
  font-weight: 500;
}

.expand-icon {
  margin-left: 16px;
  color: #00ff88;
  font-size: 1.2rem;
  transition: transform 0.3s ease;
  flex-shrink: 0;
}

.experience-card.expanded .expand-icon {
  transform: rotate(180deg);
}

.card-details {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  background:
    linear-gradient(135deg, rgba(7, 18, 30, 0.88), rgba(6, 14, 24, 0.92)),
    radial-gradient(circle at top, rgba(0, 255, 170, 0.16), transparent 65%);
  backdrop-filter: blur(38px) saturate(220%) brightness(1.1);
  -webkit-backdrop-filter: blur(38px) saturate(220%) brightness(1.1);
  position: relative;
  box-shadow:
    inset 0 1px 0 rgba(240, 246, 252, 0.1),
    inset 0 -1px 0 rgba(0, 0, 0, 0.18),
    0 6px 18px rgba(7, 12, 22, 0.35);
}

.card-details::before {
  content: '';
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 30% 20%, rgba(99, 164, 255, 0.08) 0%, transparent 55%),
    radial-gradient(circle at 70% 80%, rgba(0, 255, 170, 0.05) 0%, transparent 55%);
  pointer-events: none;
  z-index: 0;
}

.card-details > * {
  position: relative;
  z-index: 1;
}

.card-details.show {
  max-height: 1000px;
}

.responsibilities-section {
  margin-top: 20px;
  border-top: 1px solid rgba(132, 197, 255, 0.45);
  padding-top: 20px;
  position: relative;
  background:
    linear-gradient(145deg, rgba(11, 30, 49, 0.65), rgba(6, 16, 28, 0.7));
  backdrop-filter: blur(24px) saturate(200%);
  -webkit-backdrop-filter: blur(24px) saturate(200%);
  border-radius: 18px;
  padding: 20px;
  margin: 16px 0;
  box-shadow:
    inset 0 1px 0 rgba(240, 246, 252, 0.08),
    inset 0 -1px 0 rgba(0, 0, 0, 0.14),
    0 4px 12px rgba(7, 12, 22, 0.25);
}

.responsibilities-section::before {
  content: '';
  position: absolute;
  inset: 0;
  background:
    radial-gradient(circle at 18% 15%, rgba(99, 164, 255, 0.1) 0%, transparent 55%),
    radial-gradient(circle at 82% 85%, rgba(0, 255, 170, 0.08) 0%, transparent 52%);
  border-radius: 18px;
  pointer-events: none;
  z-index: 0;
}

.responsibilities-section > * {
  position: relative;
  z-index: 1;
}

.responsibilities-title {
  font-size: 1.1rem;
  font-weight: 600;
  color: #00ff88;
  margin: 0 0 16px 0;
  display: flex;
  align-items: center;
  gap: 8px;
}

.responsibilities-title::before {
  content: '';
  width: 4px;
  height: 20px;
  background: linear-gradient(135deg, #00ff88, #00cc6a);
  border-radius: 2px;
}

.responsibilities-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.responsibility-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 12px;
  padding: 8px 0;
  color: #e0e0e0;
  line-height: 1.6;
  font-size: 0.95rem;
}

.responsibility-item:last-child {
  margin-bottom: 0;
}

.responsibility-icon {
  color: #00ff88;
  font-size: 0.8rem;
  margin-top: 4px;
  flex-shrink: 0;
}

/* Responsive Design */
@media (max-width: 768px) {
  .experience-card {
    margin-bottom: 16px;
  }
  
  .card-header {
    padding: 20px;
  }
  
  .job-title {
    font-size: 1.2rem;
  }
  
  .company-name {
    font-size: 1rem;
  }
  
  .job-title-section {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .duration-info {
    flex-direction: column;
    align-items: flex-start;
    gap: 8px;
  }
  
  .responsibilities-section {
    padding: 20px;
  }
  
  .responsibility-item {
    font-size: 0.9rem;
  }
}

@media (max-width: 480px) {
  .card-header {
    padding: 16px;
  }
  
  .job-title {
    font-size: 1.1rem;
  }
  
  .company-name {
    font-size: 0.95rem;
  }
  
  .responsibilities-section {
    padding: 16px;
  }
  
  .expand-icon {
    margin-left: 8px;
  }
}

/* Animation for responsibility items */
.card-details.show .responsibility-item {
  animation: slideInUp 0.3s ease forwards;
  opacity: 0;
  transform: translateY(10px);
}

.card-details.show .responsibility-item:nth-child(1) { animation-delay: 0.1s; }
.card-details.show .responsibility-item:nth-child(2) { animation-delay: 0.15s; }
.card-details.show .responsibility-item:nth-child(3) { animation-delay: 0.2s; }
.card-details.show .responsibility-item:nth-child(4) { animation-delay: 0.25s; }
.card-details.show .responsibility-item:nth-child(5) { animation-delay: 0.3s; }
.card-details.show .responsibility-item:nth-child(n+6) { animation-delay: 0.35s; }

@keyframes slideInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}